* {
    margin: 0;
    padding: 0;
}
h1 {
    color: #4286f4;
    margin-top: 30px;
    margin-bottom: 60px;
}
#main {
    text-align: center;
    width: 80%;
    margin: 0 auto;
}
.App-logo {
    width: 150px;
    margin-top: 30px;
}
input {
    width: 100%;
    margin: 10px 0;
    padding: 18px 10px;
    border-radius: 4px;
    border: 1px solid #4e5766;
    box-shadow: 1px 0.5px #888888;
    font-size: 18px;
    box-sizing: border-box;
}

button {
    padding: 12px 10px;
    width: 30%;
    border-radius: 4px;
    background-color: #4286f4;
    color: #ffffff;
    cursor: pointer;
    font-size: 18px;
}
#no-results, #error {
    margin-top: 80px
}

#items-container {
    grid-template-columns: auto auto auto;
    grid-column-gap: 10px;
    grid-row-gap: 15px;
    margin-top: 80px;
    border: 0.1px solid #4286f4;
}
.single-item {
    box-shadow: 1px 0.5px #4286f4;
    margin: 10px;
    padding: 10px;
}

.single-item ul {
    list-style: none;
}
